* {
    box-sizing: border-box;
}
.header-hall {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 14px;
    width: 990px;
    height: 67px;
  }

.section{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 990px;
    height: 573px;
    border-radius: 2px;
    background: #F1EBE6F2;
}

.buying_info {
    display: flex;
    justify-content: space-between;
    height: 97px;
    padding: 15px 555.6px 15px 15px;
}

.buying__info-description{
    width: 419px;
    height: 67px;
}

.Heading_2{
    width: 405px;
    height: 18px;
    font-family: Roboto;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: #000000;
}

.Text-time {
    width: 137px;
    height: 16px;
    font-family: Roboto;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
}

.Text-name {
    width: 404px;
    height: 18px;
    font-family: Roboto;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: #000000;
}

.buying-scheme {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 990px;
    height: 376px;
    padding: 15px 30px;
    background-color: #171D24;
  }
  
  .buying-scheme__wrapper {
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding-top: 30px;
    max-width: 284px;
    max-height: 266px;
  }
.buying-scheme__row {
    width: 284px;
    height: 20px;
    display: flex;
    gap: 4px;
}
  .logo {
    width: 284px;
    height: auto;
    object-fit: contain;
  }
  
  .buying-scheme-chair {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    display: inline-block;
  }
  
  .buying-scheme-chair.free {
    background-color: #FFFFFF;
    border: 1px solid #525252;
  }
  
  .buying-scheme-chair.occupied {
    border: 1px solid #525252;
  }
  
  .buying-scheme-chair.vip {
    background-color: #F9953A;
    border: 1px solid #525252;
  }
  
  .buying-scheme-chair.selected {
    background-color: #25C4CE;
    border: 1px solid #525252;
  }

  .buying-scheme__legend {
    display: flex;
    width: 930px;
    height: 80px;
    padding-top: 30px;
  }
  
  .col-container {
    width: 517px;
    max-height: 50px;
    padding-left: 332.41px;
  }

  .col {
    display: flex;
    flex-wrap: wrap;
    width: 184px;
    max-height: 50px;
    max-width: 250px;
    padding-right: 14.1px;
    gap: 10px
  }

  .buying-scheme__legend-price {
    display: flex;
    width: 170px;
    max-height: 20px;
    align-items: center;
  }

  .text-price {
    width: 125px;
    height: 16px;
    font-family: Roboto;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
  }

  .text-price-vip {
    width: 150px;
    height: 16px;
    font-family: Roboto;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
  }

  .col-container-2 {
    width: 413px;
    max-height: 50px;
    min-width: 81.06px;
    padding-right: 332.41px;
  }

  .col-2 {
    display: flex;
    flex-wrap: wrap;
    width: 81px;
    max-height: 50px;
    max-width: 250px;
    gap: 10px;
  }

  .buying-scheme-chair-free {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    background-color: #FFFFFF;
    border: 1px solid #525252;
  }

  .buying-scheme-chair-vip {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    background-color: #F9953A;
    border: 1px solid #525252;
  }

  .buying-scheme-chair-occupied {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    border: 1px solid #525252;
  }

  .buying-scheme-chair-selected {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    background-color: #25C4CE;
    border: 1px solid #525252;
  }

  .booking-button {
    width: 232px;
    height: 40px;
    display: flex;
    background-color: #16A6AF;
    box-shadow: 0px 3px 3px 0px #0000003D;
    justify-content: center;
    align-items: center;
    padding: 11.2px 56.5px 11.9px 56.89px;
    border-radius: 3px;
  }

  .button-text {
    width: 119px;
    height: 16px;
    font-family: Roboto;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
  }