.section-add-film {
    width: 1600px;
    height: 900px;
    background-color:#00000080;
    background-image: url(./img/admin.jpg);
    display: flex;
    justify-content: center;
}

.popur-content {
    display: flex;
    flex-wrap: wrap;
    width: 960px;
    height: 484px;
    margin-top: 120px;
    padding-bottom: 35px;
    gap: 34px;
    background-color: #EAE9EBF2;
}

.popur-header {
    width: 100%;
    height: 57px;
    padding: 16px 42px;
    background-color: #63536C;
}

.heading-popur {
    display: flex;
    justify-content: space-between;
    width: 876px;
    height: 27px;
}

.text-popur {
    width: 248px;
    height: 25px;
    max-width: 876px;
    max-height: 25.3px;
    font-weight: 700;
    font-size: 21px;
    line-height: 25px;
    color: #FFFFFF;
    text-transform: uppercase;
}

.back-button {
    width: 22px;
    height: 22px;
    background-image: url(./img/close.png);
    background-color: #63536C;
    background-size: contain;
}

.popur-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 960px;
    height: 284px;
    padding-bottom: 8px;
    gap: 8px
}

.label-title {
    display: flex;
    flex-direction: column;
    width: 750px;
    height: 52px;
    gap: 2px;
}

.text-title {
    width: 103px;
    height: 14px;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #848484;
    margin: 0;
}

.title-input {
    width: 750px;
    height: 35px;
    border: 1px solid #b7b7b7;
    padding: 8px;
    background-color: #FFFFFF;
}

.label-duraction {
    display: flex;
    flex-direction: column;
    width: 752px;
    height: 52px;
    gap: 2px;
}

.text-duraction {
    width: 201px;
    height: 14px;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #848484;
    margin: 0;
}

.form-input {
    width: 752px;
    height: 36px;
    border: 1px solid #b7b7b7;
    background-color: #FFFFFF;
}

.label-desription {
    display: flex;
    flex-direction: column;
    width: 752px;
    height: 96px;
    gap: 2px;
}

.form-textarea {
    width: 752px;
    height: 80px;
    min-width: 752px;
    max-width: 752px;
    min-height: 80px;
    max-height: 80px;
    border: 1px solid #b7b7b7;
    background-color: #FFFFFF;
}

.text-country {
    width: 41px;
    height: 14px;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #848484;
    margin: 0;
}


.form-input::-webkit-inner-spin-button,
n {
    -webkit-appearance: none; 
    margin: 0;
}

.conf-step-buttons {
    display: flex;
    width: 561px;
    height: 40px;
    gap: 14px;
}