.container-login {
    background-image: url(img/admin.jpg);
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    width: 1600px;
    height: 900px;
    display: flex; 
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    flex-direction: column;
}

.header-login {
    width: 972px;
    height: 120px;
    padding: 35px 42px 35px 104px;
}

.heading-login {
    width: 212px;
    height: 39px;
    max-width: 826px;
    max-height: 39.1px;
    font-weight: 900;
    font-size: 34px;
    line-height: 39px;
}

.text-login {
    width: 216px;
    height: 12px;
    max-width: 826px;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 5px;
    text-transform: uppercase;
}

.section-login {
    display: flex;
    flex-direction: column;
    width: 480px;
    height: 294px;
    padding-bottom: 35px;
    gap: 35px;
    background-color: #EAE9EBF2;
}

.header-section {
    width: 480px;
    height: 57px;
    padding: 16px 104px;
    background-color: #63536C;
    display: flex;
    align-items: center;
    justify-content: center;
}

.heading-section {
    width: 161px;
    height: 25px;
    max-width: 272px;
    max-height: 25.3px;
    font-weight: 700;
    font-size: 22px;
    line-height: 25px;
    text-transform: uppercase;
}

.form-section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 480px;
    height: 167px;
}

.form-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

}
.form-group {
    display: flex;
    flex-direction: column-reverse;
    width: 272px;
    height: 52px;
    gap: 1.6px;
}

.button-block {
    display: flex;
    justify-content: center;
    width: 480px;
    height: 57px;
    padding-top: 17px;
}

.button-form {
    width: 189px;
    height: 40px;
    border-radius: 3px;
    padding: 11.2px 31.55px 11.9px 31.93px;
    background-color: #16A6AF;
    box-shadow: 0px 3px 3px 0px #0000003D;
}