* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.main-container {
    width: 990px;
    max-height: 406px;
}

.frame-2 {
    width: 990px;
    height: 81px;
}

.before-img {
    width: 990px;
    height: 3px;
    background-repeat: repeat-x;
    background-image: url(before.png);
}

.section-header {
    width: 990px;
    height: 75px;
    padding: 25px 15px;
    background-color: #F1EBE6F2;
}

.heading-2 {
    width: 960px;
    height: 25px;
    padding-right: 714px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.text-heading {
    width: 246px;
    height: 25px;
    max-width: 960px;
    max-height: 25.3px;
    font-family: Roboto;
    font-weight: 700;
    font-size: 22px;
    line-height: 25px;
    color: #C76F00;
    text-transform: uppercase;
}

.after-img {
    width: 990px;
    height: 3px;
    background-repeat: repeat-x;
    background-image: url(after.png);
}

.frame-1 {
    width: 990px;
    height: 325px;
}

.booking-confirmation {
    width: 990px;
    height: 324px;
    background-color: #F1EBE6F2;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 15px 20px;
    align-items: center;
}

.ticket-info {
    width: 960px;
    height: 18px;
    color: #000000;
}

.get-code-button {
    width: 337px;
    height: 40px;
    border-radius: 3px;
    padding: 11.2px 57px 11.9px 57px;
    background-color: #16A6AF;
    box-shadow: 0px 3px 3px 0px #0000003D;
    color: #FFFFFF;
}

.ticket-hint {
    width: 960px;
    height: 16px;
    padding-right: 102px;
}

.text-info{
    width: 858px;
    height: 16px;
    max-width: 960px;
    max-height: 16.1px;
    font-family: Roboto;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
}